<template>
  <div>
    <div style="margin:20px">{{value}}</div>

    <el-form label-width="80px">
      <el-form-item label="">
        <el-cascader
          v-model="value"
          :options="options"
          :props="{ 
            expandTrigger: 'click',
            value:'code',
            label:'name',
            children:'child'
          }"
          @change="handleChange">
        </el-cascader>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import areaList from '../../common/area.json'
export default {
  data() {
    return {
      value: [],
      options:areaList,
    };
  },

  methods: {
    handleChange(value) {
      console.log(value);
    }
  }
};
</script>
<style lang="less" scoped></style>



